<template>
    <div class="activity-info">
        <p class="title">建立活動</p>
        <p class="sub-title">Create Activity</p>
        <div class="activity-input">
            <ul>
                <li>
                    <p class="input-title">聯繫人ID</p>
                    <el-input v-model="id" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">姓名</p>
                    <el-input v-model="username" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">縣市</p>
                    <el-input v-model="city" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">鄉鎮</p>
                    <el-input v-model="township" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">E-mail</p>
                    <el-input v-model="mail" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">公司名稱</p>
                    <el-input v-model="company" placeholder="請輸入"></el-input>
                </li>
                <li>
                    <p class="input-title">活動類型</p>
                    <el-select v-model="value" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </li>
                <li>
                    <p class="input-title">活動日期</p>
                    <el-date-picker
                        v-model="date"
                        type="date"
                        placeholder="选择日期">
                    </el-date-picker>
                </li>
                <li class="activity-input-w100">
                    <p class="input-title">活動備註</p>
                    <el-input
                        type="textarea"
                        :autosize="{ minRows: 4, maxRows: 10}"
                        placeholder="请输入内容"
                        v-model="remark">
                    </el-input>
                </li>
                <li class="activity-input-w100">
                    <p class="input-title">活動ID</p>
                    <el-input v-model="activityId" :disabled="true" placeholder="儲存成功後即顯示"></el-input>
                </li>
            </ul>
            
        </div>
    </div>
</template>

<script>
export default {
    name:'activityInfo',
    components: {
    },
    data() {
        return {
            id:'123456789',
            username:'王曉明',
            city:'台中',
            township:'西屯',
            mail:'',
            company:'',
            date: '',
            activityId:'',
            remark: '',
            options: [{
                value: '选项1',
                label: '活動A'
                }, {
                value: '选项2',
                label: '活動B'
                }],
            value: '',
            }
    }


}
</script>

<style lang='scss' scoped>
.activity-info {
    padding: 100px 0;
    text-align: center;
    max-width: 850px;
    width: 80%;
    margin: 0 auto;
    .title {
        color: #333333;
        font-size: 40px;
        font-weight: bold;
    }
    .sub-title {
        color: #ef6d38;
        font-size: 25px;
    }
    .activity-input {
        margin-top: 50px;
        text-align: left;
        .input-title {
            color: #ef6d38;
            padding-left: 25px;
            margin-bottom: 10px;
            text-align: left;
        }
        ul {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            li {
                margin-top: 30px;
                padding:0 10px;
            }
        }
        .img-box {
            background: #f5f5f5;
            padding: 20px;
            text-align: center;
        }
        
    }
    .activity-input-w100 {
        width: 100%;
    }
    @media screen and (max-width: 1330px) {
        max-width: 400px;

    }
    @media screen and (max-width: 900px) {
        .activity-input ul li {
            width: 100%;
        }

    }
}

</style>


